.ordersummary{
    .card{
        display: flex;
        width: clamp(325px, 40vw, 450px);
        height: clamp(570px, 80vh, 690px);
        background: white;
        border-radius: 20px;
        overflow: hidden;
        color: hsl(223, 47%, 23%);
        box-shadow: 0 25px 30px -20px hsla(245, 75%, 52%, 0.3);
        vertical-align: middle;
        flex-direction: column;

        img{
            width: 100%;            
        }

        .card-details{
            height: 100%;
            padding: clamp(25px, 3.5vw, 45px) clamp(25px, 3.5vw, 45px);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;

            h2 {
                font-weight: 900;
                font-size: clamp(24px, 4vw, 32px);
            }

            p {
                text-align: center;
                line-height: 1.5;
                font-size: 16px;
                color: hsl(224, 23%, 55%);
            }

            .section-plan{
                width: 100%;
                height: clamp(80px, 10vh, 100px);
                background-color: hsl(225, 100%, 98%);
                display: flex;
                justify-content: space-around;
                align-items: center;
                border-radius: 10px;
                font-size: 14px;

                img{
                    width: auto;
                }

                a {
                    font-weight: bold;
                    &:visited {
                        color:hsl(245, 75%, 52%);
                    }
                }

                .plan {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    // justify-self: flex-start;
                    margin: 0 40px 0 0px;
                    font-size: clamp(14px, 3vw, 16px);
                
                    b {
                        margin-bottom: 5px;
                        font-weight: 900;
                
                    }
                
                    span {
                        font-weight: 500;
                        color: hsl(224, 23%, 55%);
                    }
                }

            }

            button {
                border: none ;
                font-weight: 900;
                font-size: 14px;
                width: 100%;
                height: 50px;
                background-color:hsl(245, 75%, 52%);
                color: #fff;
                border-radius: 10px;
                cursor: pointer;
                box-shadow: 0 20px 30px -10px hsla(245, 75%, 52%, 0.5);
                
                &:hover {
                    background-color: hsla(245, 75%, 52%, 0.8);
                }
            }

        }
    }
}